<template>
  <div class="content">
    <div class="table" id="printDiv" >
      <table border="1">
        <caption></caption>
        <caption></caption>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>:</td>
            <td colspan="2" align="center">QYYS ZXKS 2023-003</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="exportBtn" @click="exportExcel"></div>
    <div class="exportBtn" v-print="printContent"></div>
  </div>
</template>

<script>
  import { Row } from 'element-ui';
import ExcelJS from 'exceljs';
  import FileSaver from "file-saver";
  import Vue from 'vue';
  import Print from 'vue-print-nb'
  Vue.use(Print)
  
  
  export default {
    data() {
      return {
        printContent: {
          id: 'printDiv',
          // preview: true, // 
          // previewTitle: '', // 
          // popTitle: '', // 
          extraCss: "https://cdn.bootcdn.net/ajax/libs/animate.css/4.1.1/animate.compat.css, https://cdn.bootcdn.net/ajax/libs/hover.css/2.3.1/css/hover-min.css",
          extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>',
          previewBeforeOpenCallback() {
            console.log('')
          },
          previewOpenCallback() {
            console.log('')
          },
          beforeOpenCallback(vue) {
            vue.printLoading = true
            console.log('')
          },
          openCallback(vue) {
            vue.printLoading = false
            console.log('')
          },
          closeCallback() {
            console.log('')
          },
          clickMounted(vue) {
            console.log('')
            // vue.printSet.popTitle = vue.tableHead // 
          },
        },
        headData: [{
            cellName: "A1",
            mergeName: "A1:G1",
            value: "",
            style: {
              vertical: "middle",
              horizontal: "center",
            },

          },
          {
            cellName: "A2",
            mergeName: "A2:G2",
            value: "",
            style: {
              vertical: "middle",
              horizontal: "center",
            }
          },
          {
            cellName: "E5",
            mergeName: "",
            value: ":",
            style: null,
          },
          {
            cellName: "F5",
            mergeName: "F5:G5",
            value: "QYYS ZXKS 2023-003",
            style: {
              vertical: "middle",
              horizontal: "center",
            },
          },
          {
            cellName: "A7",
            mergeName: "A7:B7",
            value: ":",
            style: null,
          },
          {
            cellName: "C7",
            mergeName: "C7:G7",
            value: "",
            style: null,
          },
          {
            cellName: "A9",
            mergeName: "A9:B9",
            value: ":",
            style: null,
          },
          {
            cellName: "C9",
            mergeName: "C9:G9",
            value: "2023 12  21  2023 12  21",
            style: null,
          }
        ],
        listData: [
          [1, "", 2150, 18, "1%", 38713.86, 383.31],
          [2, "", 2150, 18, "1%", 38713.86, 383.31]
        ]
      }
    },
    methods: {
      exportExcel() {
        const excel_name = "test.xlsx";
        // 
        const wb = new ExcelJS.Workbook();
        // 
        const sheet = wb.addWorksheet("sheet");
        this.setTableContent(sheet);
        wb.xlsx.writeBuffer().then((buffer) => {
          let file = new Blob([buffer], {
            type: "application/octet-stream",
          });
          FileSaver.saveAs(file, "ExcelJS.xlsx");
        });
      },
      // 
      setTableContent(sheet) {
        sheet.columns = [{
            width: 10
          },
          {
            width: 20
          },
          {
            width: 10
          },
          {
            width: 20
          },
          {
            width: 15
          },
          {
            width: 20
          },
          {
            width: 20
          },
        ];
        for (var i = 0; i < this.headData.length; i++) {
          this.createHeadCell(this.headData[i], sheet);
        }
        sheet.addRow([]);
        sheet.addRow(['', '', '', '()', '', '()', '()']);
        sheet.getRow(11).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          cell.font = {
            size: 16,
            bold: true
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addTable({
          ref: 'A12',
          headerRow: false,
          totalsRow: false,
          columns: [
            {name: ''},
            {name: ''},
            {name: ''},
            {name: '()'},
            {name: ''},
            {name: '()', totalsRowFunction: 'sum'},
            {name: '()', totalsRowFunction: 'sum'},
          ],
          rows: this.listData,
        });
        sheet.addRow(['', null, null, null, null, 38713.86, 38713.86]);
        sheet.addRow([':', null, 95785.4, null, null, null, null]);
        sheet.addRow([':', null, 957.85, null, null, null, null]);
        sheet.addRow([':', null, 96743.26, null, null, null, null]);
        //  K10:M12
        sheet.mergeCells(12 + this.listData.length,1,12 + this.listData.length,5);
        for (var i = 0; i < this.listData.length; i++) {
          this.setRowStyle(12 + i, sheet);
        }
        this.setRowStyle(12 + this.listData.length, sheet);
        for (var i = 0; i < 3; i++) {
          sheet.mergeCells(13 + i + this.listData.length,1,13 + i + this.listData.length,2);
          sheet.mergeCells(13 + i + this.listData.length,3,13 + i + this.listData.length,7);
          this.setBottomRowStyle(13 + i + this.listData.length, sheet);
        }
        sheet.addRow();
        sheet.addRow();
        sheet.addRow([':', null, null, null, ':', null, null]);
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,6);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addRow();
        sheet.addRow();
        sheet.addRow([':', null, ':', null, ':', null, null]);
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,3,sheet._rows.length,4);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,6);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addRow();
        sheet.addRow();
        sheet.addRow(['', null, '', null, '', null, null]);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,3,sheet._rows.length,4);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,7);
      },
      //
      createHeadCell(data, sheet) {
        const titleCell = sheet.getCell(data.cellName);
        if (data.mergeName) {
          sheet.mergeCells(data.mergeName);
        }
        if (data.cellName == "A1" || data.cellName == "A2") {
          titleCell.font = {
            size: 18,
          };
        } else {
          titleCell.font = {
            size: 16,
          };
        }
        if (data.cellName == "A1") {
          titleCell.border = {
            bottom: {
              style: "medium",
              color: {
                argb: "00FFFFFF",
              }
            }
          }
        }
        titleCell.value = data.value;
        titleCell.alignment = data.style;
      },
      // 
      setRowStyle(row, sheet) {
        sheet.getRow(row).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          if (row == 12 + this.listData.length) {
            cell.font = {
              size: 14,
              bold: rowIdx == 6 || rowIdx == 7 ? false : true
            }
          } else {
            cell.font = {
              size: 14,
              bold: rowIdx == 1 ? true : false
            }
          }
          
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
      },
      // 
      setBottomRowStyle(row, sheet) {
        sheet.getRow(row).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          cell.font = {
            size: 14,
            bold: rowIdx == 1 || rowIdx == 2 ? true : false
          }
          if (rowIdx == 1 || rowIdx == 2) {
            cell.alignment  = {
              vertical: "middle",
              horizontal: "right",
            }
          }else {
            cell.alignment  = {
              vertical: "middle",
              horizontal: "left",
            }
          }
        });
      }
    }
  }
</script>

<style >
  .table {
    width: calc(100% - 40px);
    padding: 20px;
  }

  .table table {
    width: 100%;
    table-layout: fixed;
    border-color: #eeeeee;
    border-spacing: 0;
    border-collapse: collapse;
  }

  table caption {
    color: #222222;
    font-size: 24px;
  }

  table tr td {
    height: 30px;
  }

  .exportBtn {
    margin: 20px;
    height: 40px;
    width: 100px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    background-color: #4169e1;
    color: white;
    border-radius: 4px;
    cursor: pointer;
  }
</style>
